import React, { useEffect, useState ,useContext} from "react";
import logo from "../../assets/tea-leaf.png"
import lbg from "../../assets/lbg.webp"
import { useNavigate } from "react-router-dom"
import { useDispatch, useSelector } from "react-redux";
import {loginUser,signupUser} from "../../store/loginSlice";
import {getAllfactory } from "../../store/catalogueSlice";
import "../../login.css";
export default function Login() {
  const history = useNavigate();

  const { factory_list } = useSelector((state) => state.catalogue);
  const dispatch = useDispatch();

  const [authMode, setauthMode] = useState(false);
  const [loginDetails, setLogindetails] = useState({

  });

  

  let name, value;
  const getUserData = (event) => {
    name = event.target.name;
    value = event.target.value;
  
    setLogindetails({ ...loginDetails, [name]: value });
  };


  
  const login = (e) => {
    e.preventDefault()
    dispatch(loginUser({loginDetails})).unwrap()
    .then(() => {
      history("/dashboard")
    })
    .catch(() => {
      history("/")
    });
  };
  const signup = (e) => {
    e.preventDefault()
    dispatch(signupUser({loginDetails})).unwrap()
    .then(() => {
      alert('Users create successfully done')
    })
    .catch(() => {
     alert('error')
    });
  };

  useEffect(() => {
    dispatch(getAllfactory());
    // dispatch(getAllcatalogue());

    
  }, []);
if(authMode===true){


  return (
    <div className="Auth-form-container">
<div className="container cbody">
  {/* Outer Row */}
  <div className="row justify-content-center">
    <div className="col-xl-10 col-lg-12 col-md-9">
      <div className="card o-hidden border-0 shadow-lg my-5">
        <div className="card-body p-0">
          {/* Nested Row within Card Body */}
          <div className="row">
            <div className="col-lg-6 d-none d-lg-block bg-login-image" >
            <img src={lbg} width="100%" height="100%" alt=""/>
            </div>
            <div className="col-lg-6">
              <div className="p-5">
                <div className="text-center">
                  <h1 className="h4 text-gray-900 mb-4">Welcome Back!</h1>
                </div>
                <form className="user">
                  <div className="form-group">
                  <input
            autoComplete="off"
            id="fullname"
            name="fullname"
              onChange={getUserData}
              type="text"
              className="form-control mt-1"
              placeholder="Full name"
            />
            </div>
                  <div className="form-group">
                  <select   className="form-control mt-1" name="company" id="company"  onChange={getUserData}>
                        <option value=""> -- Select Type -- </option>
                        {/* Mapping through each fruit object in our fruits array
                 and returning an option element with the appropriate attributes / values.
                          */}
                        {factory_list.map((shead) => (
                          <option value={shead.id}>{shead.name}</option>
                        ))}
                      </select>
                      
                        </div>

                        <div className="form-group">
                        <input
              autoComplete="off"
             onChange={getUserData}
              type="text"
              id="s_username"
              name="s_username"
              className="form-control mt-1"
              placeholder="Username"
            />
            </div>

            <div className="form-group">
            <input
              autoComplete="off"
             onChange={getUserData}
              type="password"
              id="s_password"
              name="s_password"
              className="form-control mt-1"
              placeholder="Password"
            />
            </div>

            <div className="form-group">
            <select   className="form-control mt-1" name="type" id="type"  onChange={getUserData}>
            <option value="">SELECT</option>
  <option value="BROKERS">BROKERS</option>
  <option value="BIDDERS">BIDDERS</option>
  <option value="WAREHOUSE">WAREHOUSE</option>
  <option value="ASSOSIATION">ASSOSIATION</option>
</select>
            </div>
                  
                  <a href="index.html" onClick={signup} className="btn btn-primary btn-user btn-block">
                    Signup
                  </a>
              

                </form>
                <hr />
                <div className="text-center">
                  <a className="small" >Forgot Password?</a>
                </div>
                <div className="text-center">
                  <a className="small" onClick={()=>{setauthMode(false)}}>Alrready have an Account!</a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>




      
    </div>
  )
  

}else{
  return (
    <div className="Auth-form-container">




      {/* <div className="registration">
        <h1>Registration</h1>
        <label>Username</label>
        <input
          type="text"
          onChange={(e) => {
            setUsernameReg(e.target.value);
          }}
        />
        <label>Password</label>
        <input
          type="text"
          onChange={(e) => {
            setPasswordReg(e.target.value);
          }}
        />
        <button onClick={register}> Register </button>
      </div>

      <div className="login">
        <h1>Login</h1>
        <input
        name="username"
          type="text"
          placeholder="Username..."
          onChange={getUserData}
        />
        <input
        name="password"
          type="password"
          placeholder="Password..."
          onChange={getUserData}
        />
        <button onClick={login}> Login </button>
      </div>

      <h1>{loginStatus}</h1> */}





<div className="container cbody">
  {/* Outer Row */}
  <div className="row justify-content-center">
    <div className="col-xl-10 col-lg-12 col-md-9">
      <div className="card o-hidden border-0 shadow-lg my-5">
        <div className="card-body p-0">
          {/* Nested Row within Card Body */}
          <div className="row">
            <div className="col-lg-6 d-none d-lg-block bg-login-image" >
            <img src={lbg} width="100%" height="100%" alt=""/>
            </div>
            <div className="col-lg-6 d-grid align-items-center">
              <div className="p-5">
              <div className="hero">
   <img src={logo} alt="Logo" height="50px" className="mb-3" />
   </div>
                <div className="text-center">
                  <h1 className="text-gray-900 mb-4">Welcome Back!</h1>
                </div>
                <form className="user">
                  <div className="form-group">
                  <input
        className="form-control mt-1"
        name="username"
          type="text"
          placeholder="Username..."
          onChange={getUserData}
          autoComplete="off"
        />  </div>
                  <div className="form-group">
                  <input
         className="form-control mt-1"
         name="password"
          type="password"
          placeholder="password"
          autoComplete="off"
          onChange={getUserData}
        />    </div>
                 
                  <a href="index.html" onClick={login} className="btn btn-primary btn-user btn-block btn-large">
                    Login
                  </a>
              

                </form>
                <hr />
                <div className="text-center">
                  <a className="large" href="forgot-password.html">Forgot Password?</a>
                </div>
                <div className="text-center">
                  <a className="large" onClick={()=>{setauthMode(true)}} >Create an Account!</a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>





      
    </div>
  );
}
  
}
